import { enableProdMode } from '@angular/core';
import { platformBrowserDynamic } from '@angular/platform-browser-dynamic';
import { AppModule } from './app/app.module';
import { APP_CONFIG, AppConfig } from './app-config';

void fetch('/assets/app-config.json')
  .then((response) => {
    if (!response.ok) {
      throw new Error('Failed to load configuration from server.');
    }
    return response.text();
  })
  .then((config: string) => {
    const appConfig = JSON.parse(config) as AppConfig;
    if (appConfig.production) {
      enableProdMode();
    }
    const appConfigProvider = { provide: APP_CONFIG, useValue: appConfig };
    return platformBrowserDynamic([appConfigProvider]).bootstrapModule(AppModule, {
      preserveWhitespaces: true,
    });
  })
  .catch((err) => console.error(err));
