import { Component } from '@angular/core';
import { isCustomMaxWidthComponent } from './bedag-components/layout/custom-max-width-component';
import { TitleTagService } from './styleguide/demo-utils/routing/title-tag.service';
import { LanguageService } from './bedag-components/i18n/language.service';
import { NavigationService } from './bedag-components/navigation/navigation.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
})
export class AppComponent {
  maxContentWidth = '70em';

  constructor(
    private readonly titleTagService: TitleTagService,
    private readonly languageService: LanguageService,
    public readonly navigationService: NavigationService
  ) {
    this.titleTagService.init('Bedag Angular Styleguide');
    this.languageService.currentLanguage = 'de';
  }

  /**
   * Configure Standard width of the content area. It can be overwritten in individual components.
   */
  onRouterOutletActivate(component: unknown): void {
    if (isCustomMaxWidthComponent(component)) {
      this.maxContentWidth = component.maxWidth;
    } else {
      this.maxContentWidth = '70em';
    }
  }
}
