import { Injectable } from '@angular/core';
import { LanguageService } from '../i18n/language.service';

@Injectable({
  providedIn: 'root',
})
export class NavigationService {
  private readonly NAV_BREAKPOINT = 768;

  private navigationClosed: boolean;
  public navigationToggleText = '';

  private readonly closeNavigationText: { [index: string]: string } = {
    en: 'Close navigation',
    de: 'Navigation zuklappen',
    fr: 'Fermer la navigation',
    it: 'Chiudere la navigazione',
  };
  private readonly openNavigationText: { [index: string]: string } = {
    en: 'Open navigation',
    de: 'Navigation aufklappen',
    fr: 'Ouvrir la navigation',
    it: 'Aprire la navigazione',
  };

  constructor(private readonly languageService: LanguageService) {
    this.navigationClosed = this.isMobile();
    this.updateNavigationText();
    document.body.classList.toggle('close-navigation', this.navigationClosed);
  }

  public get isNavigationClosed(): boolean {
    return this.navigationClosed;
  }

  toggleNavigation(): void {
    const body = document.body;
    if (body && body.classList) {
      this.navigationClosed = body.classList.toggle('close-navigation');
      this.updateNavigationText();
    }
  }

  private updateNavigationText(): void {
    if (this.navigationClosed) {
      this.navigationToggleText = this.openNavigationText[this.languageService.currentLanguage];
    } else {
      this.navigationToggleText = this.closeNavigationText[this.languageService.currentLanguage];
    }
  }

  isMobile(): boolean {
    return window.innerWidth <= this.NAV_BREAKPOINT;
  }
}
